import type { AddressItem, AddressParams } from '@/types/address.d.ts'
import { http2 } from '@/utils/http'

/**
 * 添加收货地址
 * @param data 请求参数
 */
export const postMemberAddressAPI = (
    data: AddressParams & {
        provinceName: string
        cityName: string
        districtName: string
    },
) => {
    return http2({
        method: 'POST',
        url: '/user/addressBook',
        data,
    })
}
/**
 * 获取收货地址列表
 */
export const getMemberAddressAPI = () => {
    return http2<AddressItem[]>({
        method: 'GET',
        url: '/user/addressBook',
    })
}

/**
 * 获取收货地址详情
 * @param id 地址id(路径参数)
 */
export const getMemberAddressByIdAPI = (id: string) => {
    return http2<AddressItem>({
        method: 'GET',
        url: `/user/addressBook/${id}`,
    })
}
/**
 * 修改收货地址
 * @param id 地址id(路径参数)
 * @param data 表单数据(请求体参数)
 */
export const putMemberAddressByIdAPI = (
    id: string,
    data: AddressParams & {
        provinceName: string
        cityName: string
        districtName: string
    },
) => {
    return http2({
        method: 'PUT',
        url: `/user/addressBook/${id}`,
        data,
    })
}

/**
 * 删除收货地址
 * @param id 地址id(路径参数)
 */
export const deleteMemberAddressByIdAPI = (id: string) => {
    return http2({
        method: 'DELETE',
        url: `/user/addressBook/${id}`,
    })
}
